
html,
body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
}
#app {
  background: #333;
  color: white;
  font-family: "\5FAE\8F6F\96C5\9ED1";
  font-size: 18px;
  width: 100%;
  height: 100%;
}
#canvas {
  position: absolute;
  left: calc(50% - 500px);
  top: calc(50% - 400px);
}
#game_intro {
  width: 550px;
  text-align: center;
  position: fixed;
  left: calc(50% - 275px);
  bottom: 10px;
}
